@import url(https://fonts.googleapis.com/css?family=Barlow|Nova+Mono&display=swap);
article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

a:focus {
  outline: thin dotted; }

a:active, a:hover {
  outline: 0; }

h1 {
  font-size: 2em; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: \201C \201D \2018 \2019; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em; }

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

button, html input[type=button], input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled], input[disabled] {
  cursor: default; }

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=search] {
  -webkit-appearance: textfield;
  box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body, figure {
  margin: 0; }

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: '';
  clear: both;
  height: 0; }

* {
  box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0);
  overflow: hidden; }

h1, h2, h3, p {
  margin: 0; }

ul, li {
  padding: 0;
  list-style: none; }

a {
  text-decoration: none;
  color: inherit; }

html {
  font-size: 125%;
  scroll-behavior: smooth; }

.wrapper {
  width: 80%;
  max-width: 800px;
  margin: 0 auto; }

body {
  background: #cceeff;
  font-size: 1rem;
  color: #000000;
  font-family: "Barlow", sans-serif; }

body > div, .App {
  min-height: 100vh; }

.App {
  display: flex;
  flex-direction: column; }
  .App > div {
    flex-grow: 1; }

header {
  padding: 40px 0;
  background: #006699;
  background-image: url(/laurenHetheringtonProjectFive/static/media/bright-squares.53c1ec5f.png);
  color: #ffffff; }
  header h1 {
    font-size: 2rem;
    text-align: center;
    text-shadow: 3px 3px 0 #ff00aa; }

h1, h2, h3, button {
  font-family: "Nova Mono", monospace;
  font-weight: normal; }

main {
  padding: 20px 0; }

form {
  display: flex;
  flex-wrap: wrap; }
  form.questionForm {
    margin-bottom: 30px; }
  form.answerForm {
    margin: 30px 0; }
  form label {
    font-family: "Nova Mono", monospace;
    display: block;
    width: 100%; }
  form input {
    size: 1;
    flex-grow: 1;
    flex-shrink: 1;
    padding: 5px 10px;
    border: none; }

button {
  background: #ff00aa;
  color: #000000;
  border: #ff00aa 2px solid;
  padding: 5px 20px;
  -webkit-transition: 0.3s;
  transition: 0.3s; }
  button:hover, button:focus {
    color: #ff00aa;
    background: #000000; }
  button:disabled {
    color: #000000;
    background: gray;
    border-color: gray; }

ul li {
  display: flex;
  margin: 20px 0;
  align-items: stretch;
  -webkit-transition: 0.3s;
  transition: 0.3s; }

ul.questionList li {
  position: relative;
  background: #99ddff;
  box-shadow: 10px 10px 0 #00aaff;
  top: 0;
  left: 0; }
  ul.questionList li:hover, ul.questionList li:focus-within {
    box-shadow: 12px 12px 0 #00aaff;
    left: -2px;
    top: -2px; }

ul.questionThread li {
  background: #006699;
  box-shadow: 10px 10px 0 #00aaff;
  color: #ffffff; }

.textContent {
  flex-grow: 1; }
  .textContent a {
    display: block;
    width: 100%;
    -webkit-transition: 0.3s;
    transition: 0.3s; }
    .textContent a:hover, .textContent a:focus {
      color: #ff00aa; }
  .textContent a, .textContent.answerText {
    padding: 10px; }

.voteKnob {
  display: flex;
  align-items: stretch;
  background: #ffffff;
  color: #000000; }
  .voteKnob p {
    align-self: center;
    padding: 0 5px;
    text-align: center; }
  .voteKnob button {
    padding: 2px 10px; }
  .voteKnob p, .voteKnob button {
    min-width: calc(2rem); }

.returnButton {
  display: block;
  margin: 20px auto; }

h2 {
  margin: 20px 0; }

footer {
  padding: 40px 0;
  background: #006699;
  background-image: url(/laurenHetheringtonProjectFive/static/media/bright-squares.53c1ec5f.png);
  color: #ffffff;
  text-align: center;
  text-shadow: 1px 1px 0 #ff00aa; }

@-webkit-keyframes moveDown {
  from {
    -webkit-transform: translateY(calc(-100% - 20px));
            transform: translateY(calc(-100% - 20px)); }
  to {
    -webkit-transform: translateY(0%);
            transform: translateY(0%); } }

@keyframes moveDown {
  from {
    -webkit-transform: translateY(calc(-100% - 20px));
            transform: translateY(calc(-100% - 20px)); }
  to {
    -webkit-transform: translateY(0%);
            transform: translateY(0%); } }

@-webkit-keyframes moveUp {
  from {
    -webkit-transform: translateY(calc(100% + 20px));
            transform: translateY(calc(100% + 20px)); }
  to {
    -webkit-transform: translateY(0%);
            transform: translateY(0%); } }

@keyframes moveUp {
  from {
    -webkit-transform: translateY(calc(100% + 20px));
            transform: translateY(calc(100% + 20px)); }
  to {
    -webkit-transform: translateY(0%);
            transform: translateY(0%); } }

.moveUp {
  -webkit-animation: 0.3s moveUp;
          animation: 0.3s moveUp; }

.moveDown {
  -webkit-animation: 0.3s moveDown;
          animation: 0.3s moveDown; }

@media (max-width: 650px) {
  form {
    flex-direction: column; }
    form label {
      text-align: center; } }

@media (max-width: 450px) {
  ul li {
    flex-wrap: wrap; }
  .voteKnob {
    width: 100%; }
    .voteKnob p, .voteKnob button {
      flex-grow: 1; } }

